:root
{
  /* Colors */
  --color-default-blue: #4738da;
  --color-bluedark: #403f8881;
  --color-default-black: #313131;
  --color-default-white: #f1f1f1;
  --color-blueviolet: #8a2be2;

  /* Border */
  --border-size: 8px; 

}

*
{
  box-sizing: border-box;

  padding: 0;
  margin: 0;
}

body, html
{
  width: 100%;
  height: 100%;

  background-color: #313131;

  display: grid;
  place-items: center;

  background-image: url(../assets/background.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  background-blend-mode: color-burn;
}