.calculadora
{
  border: 2px solid var(--color-default-blue);
  border-radius: var(--border-size);

  background-color: var(--color-default-black);

  width: 22rem;
  height: 30rem;

  @media (max-width: 400px){
    width: 19rem;
  }
}

.text-container
{
  width: 100%;
  height: 5rem;
  border-radius: var(--border-size);

  > input[type="text"]
  {
    background-color: var(--color-default-black);
    border-radius: var(--border-size);
    letter-spacing: 4px;
    border: none;
    outline: none;

    height: 100%;
    width: 100%;
    padding: 0 19px;
    
    text-align: right;
    color: var(--color-default-white);
    font: 400 28px Arial, sans-serif;
  }
}

.container
{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(70px, auto);

  gap: 10px;
  padding: 0 10px;

  #zero
  { 
    grid-column: 1/3;
    grid-row: 5;
  }

  #equal
  {
    grid-row: 4/6;
    grid-column: 4;
  }

  > button
  {
    background: none;
    outline: none;

    color: var(--color-default-white);
    border: 1px solid #474747;

    border-radius: var(--border-size);

    font: bolder 20px Arial, sans-serif;

    &:hover
    {
      animation: animation .119s;
    }

    &:active
    {
      border: 2px solid var(--color-default-blue);
    }

    &.calc
    {
      background: linear-gradient(220deg,
      var(--color-bluedark),
      var(--color-default-black));

      border-color: var(--color-default-blue);
      color: var(--color-blueviolet);
    }
  }
}


@keyframes animation {
  0%
  {
    background: linear-gradient(135deg, 
    var(--color-default-blue),
    var(--color-bluedark),
    var(--color-bluedark));
  }

  50%
  {
    background: linear-gradient(135deg, 
    var(--color-bluedark), 
    var(--color-default-blue), 
    var(--color-bluedark));
  }

  100%
  {
    background: linear-gradient(135deg, 
    var(--color-bluedark), 
    var(--color-bluedark), 
    var(--color-default-blue));
  }
}